let initialState = {
    dialogsName: [
        {id: 1, name: ' .'},
        {id: 2, name: ' '},
        {id: 3, name: ' .'},
        {id: 4, name: ' .'},
        {id: 5, name: ' .'},
        {id: 6, name: ' .'},
        {id: 7, name: ' .'},
        {id: 8, name: ' .'},
        {id: 9, name: ' .'},
        {id: 10, name: ' .'}
    ],
    dialogsMessage: [
        {id: 1, message: "!    ?"},
        {id: 2, message: "! !   ?"},
        {id: 3, message: "    "},
        {id: 4, message: " ,  "}
    ]
}

const dialogsReducer = (state = initialState, action) =>{

    switch(action.type) {
        case("AddMessage"):
            let newMessageText = {
                id: 5,
                message: action.newMessage
            };
            return {
                ...state,
                dialogsMessage: [...state.dialogsMessage, newMessageText]
            }
            break;
        case("UpdateNewMessage"):
            return {
                ...state,
                newMessage: action.newText
            }
            break;
    }
    return state;
}

export const addMessage = (newMessage) =>{
    return{
        type:'AddMessage',
        newMessage
    }
}

export default dialogsReducer;